    top: -45.5rem !important;
    left: -12rem !important;
  }
}

[class\$=balloon-overlay] [class\$=balloon__tail] {
  display: none;
}

[class\$=balloon-overlay] [class\$=close-button] {
  margin-right: 0.3rem;
}

.product-reviews__header-box {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 6rem;
}
.product-reviews__btn {
  background-color: #c6ef4e;
}
.product-reviews__btn svg {
  fill: #0e1c15;
}
.product-reviews__reviews-list {
  display: flex;
  flex-direction: column;
  gap: 2.4rem;
}
.product-reviews__review-item {
  cursor: pointer;
  background-color: #f6f6f6;
  border-radius: 4rem;
  padding: 4rem 48.2rem 4rem 4rem;
  display: flex;
  flex-direction: column;
  gap: 3.2rem;
  justify-content: space-between;
  width: 136rem;
}
.product-reviews__review-item-title {
  position: relative;
  z-index: 2;
}
.product-reviews__review-item-title span::before {
  height: 3.6rem;
}
.product-reviews__review-item-text {
  display: -webkit-box;
  -webkit-line-clamp: 2; /* Максимальное количество строк */
  -webkit-box-orient: vertical;
  overflow: hidden;
  text-overflow: ellipsis;
}
.product-reviews__review-item-footer {
  display: flex;
  flex-direction: column;
  gap: 2rem;
}
.product-reviews__review-item-footer-info {
  display: flex;
  align-items: center;
  gap: 1.6rem;
}
.product-reviews__review-item-photo-box {
  width: 6rem;
  height: 6rem;
  border-radius: 50%;
  overflow: hidden;
}
.product-reviews__review-item-photo-box img {
  object-fit: contain;
}
.product-reviews__button {
  margin-top: 2.4rem;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
}
.product-reviews__button::after {
  content: "";
  display: block;
  width: 100%;
  height: 0.1rem;
  margin-top: 0.2rem;
  background-color: #0e1c15;
  transition: width 0.3s;
}
.product-reviews__button:hover:after {
  width: 0%;
}

@media (max-width: 48em) {
  .product-reviews__header-box {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 2.4rem;
    margin-bottom: 3.2rem;
  }
  .product-reviews__btn {
    width: 100%;
  }
  .product-reviews__review-item {
    padding: 4rem;
    width: 100%;
  }
  .product-reviews__review-item-title {
    position: relative;
    z-index: 2;
  }
  .product-reviews__review-item-title span::before {
    height: 3.6rem;
  }
  .product-reviews__review-item-text {
    -webkit-line-clamp: 3;
  }
  .product-reviews__review-item-footer {
    gap: 2.4rem;
    flex-direction: column;
    align-items: flex-start;
  }
  .product-reviews__review-item-footer-info {
    gap: 1.6rem;
  }
  .product-reviews__review-item-photo-box {
    width: 8rem;
    height: 8rem;
  }
  .product-reviews__button:hover:after {
    width: 100%;
  }
}
.watswpp__btn-link {
  z-index: 10;
  position: fixed;
  bottom: 8rem;
  right: 4rem;
}
@media (max-width: 48em) {
  .watswpp__btn-link {
    display: none;
  }
